.no-overflow {
  overflow: hidden !important;
}

.Panel.centering-panel .Panel__in {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  text-align: center;
}

.cropper {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.opacity-image {
  overflow: hidden;
  width: 100%;
}

.opacity-image::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.6);
  z-index: 100;
}

.opacity-image img {
  max-width: 100%;
  min-width: 100%;
}

.crop {
  position: absolute;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  z-index: 120;
  /*border: solid 1px #fff;*/
}

.cropped-image {
  /*position: absolute;*/
  width: 100px;
  /*top: 0;*/
  /*left: 0;*/
  height: 100px;
  overflow: hidden;
  border-radius: 12px;
  background-color: white;
}

.crop .crop-controller {
  display: block;
  position: absolute;
  background: #fff;
  width: 22px;
  height: 22px;
  border-radius: 100%;
  z-index: 120;
  box-shadow: 0px 0px 5px rgba(0,0,0,.2);
  border: solid 2px transparent;
}

.crop .crop-controller.top-left {
  top: 0;
  left: 0;
}

.crop .crop-controller.top-right {
  top: 0;
  right: 0;
}

.crop .crop-controller.bottom-left {
  bottom: 0;
  left: 0;
}

.crop .crop-controller.bottom-right {
  bottom: 0;
  right: 0;
  margin-right: -4px;
  margin-bottom: -4px;
}

.cropped-image {
  /*transform: translateX(33vw) translateY(20vh);*/
}

.cropped-image img {
  max-width: 100vw;
  min-width: 100vw;
  /*transform: translateX(-33vw) translateY(-20vh);*/
}

.wrap-cropper {
  position: relative;
  z-index: 1;
}

.wrapped-image {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 100%;
  bottom: 0;
  left: 0;
  background-size: cover;
  background-repeat: no-repeat;
}

.wrapped-image::after {
  content: "";
  display: block;
  height: 100%;
  left: 0;
  right: 0;
  position: absolute;
  bottom: 0;
  top: 0;
  background-color: rgba(255,255,255,.8);
}

body:not([scheme]) .Button--lvl-secondary,
body[scheme="client_light"] .Button--lvl-secondary {
  background-color: #E2E8EE;
}

body[scheme="client_dark"] .wrapped-image::after {
  background-color: rgba(44,45,46,.9);
}